import React from 'react'
import { Line } from "react-chartjs-2";
// import "chartjs-plugin-lineheight-annotation";
import numeral from 'numeral'
import { numberWithCommas } from '../Utility';



export const LineChart = ({ text, amount }) => {

  // let amount = transaction.map(amount => transaction.amount)
  console.log(text, amount);
  //FIXME Construction (Figuring out how to change graph color based on amount value)
  const changeColor = () => {
    let tableData = amount
    for (let i = 0; i < tableData.length; i++) {
      var color = "rgb(0, 255, 0)";
      //You can check for tableData[i].value and put your conditions here
      if (tableData[i].value <= -1) {
        color = "rgba(255,99,132,0.2)"; // Red
      }
      else {
        color = "rgb(0, 255, 0)"// Green
      }
      tableData[i].fillColor = color;
    }
  }



  const data = {
    labels: text,
    datasets: [
      {
        label: 'Transactions',
        backgroundColor: 'rgba(255,99,132,0.2)',
        // backgroundColor: changeColor(),//FIXME
        borderColor: 'rgba(255,99,132,1)',
        borderWidth: 1,
        hoverBackgroundColor: 'rgba(255,99,132,0.4)',
        hoverBorderColor: 'rgba(255,99,132,1)',
        data: amount
      }
    ]
  }


  return (
    <div id="line">
      <Line
        data={data}
        width={1000}
        height={500}
        options={{
          title: {
            display: true,
            text: 'Spending Chart',
            fontSize: 25
          },
          responsive: true,
          legend: { display: true },
          maintainAspectRatio: false,
          responsiveAnimationDuration: 0,
          scales: {
            yAxes: [{
              ticks: {
                callback: function (value, index, values) {
                  // add comma as thousand separator
                  return numberWithCommas(value)
                },
              }
            }]
          },
          tooltips: {
            callbacks: {
              label: function (tooltipItem, data) {
                var label = data.datasets[tooltipItem.datasetIndex].label || ''

                if (label) {
                  label += ': '
                }
                label += numeral(tooltipItem.yLabel).format('0,0')
                return label
              },
            },
          },
        }}
      />
    </div>
  )
}
